import React, { Suspense, useState } from "react";
import { Card, CardBody } from "react-bootstrap";
import TopPerfomingSkeleton from "../../layout/skeleton/topPerfomingSkeleton";
import NoData from "../../layout/common/noData";
import { SENTIMENTS_CALC } from "../../../../../utils";
import SeeMoreModal from "../../layout/common/seeMoreModal";
import AuthorInfo from "../content/authorInfo";
import PostMedia from "../../layout/common/postMedia";
import { getPrfomingPostMore, getreactedpostsMore } from "../../../../../actions/analyticsNew";
import PostdetailPopup from "../../layout/popup/postdetailPopup";
import SectionHeading from "../../layout/common/sectionHeading";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import PercentDifference from "../../layout/common/percentDifference";
const PostPopup = React.lazy(() => import("../../layout/popup/postPopup"));

const PerfomingPost = (props) => {
const { type, title, postData, loader, totalCount } = props;

const [show, setShow] = useState(false);
const [showPost, setshowPost] = useState(false);
const [postInfo, setpostInfo] = useState(false);

const handlePopup = (event) => {
    setShow(event);
};

const handlePostpopup = (event) => {
    setshowPost(event);
  };

  const canSlide = postData && Object.keys(postData).length>6;
  const perPagePost = canSlide ? 6 : postData ? Object.keys(postData).length : 0
 
  const settings = {
    type: 'loop',
    rewind: false,
    speed: 1000,
    perMove: 1,
    loop: canSlide,
    interval: 5000,
    drag: canSlide,   
    perPage: perPagePost,
    gap: 0,
    autoplay: canSlide,
    pagination: false,
    arrows: canSlide,
    fixedHeight: true,
    breakpoints: {
        0: {
            perPage: 1
        },
        579: {
            perPage: 2
        },
        767: {
            perPage: 3,
        },
        1300: {
            perPage: 4,
        },
        1600: {
            perPage: 5,
        }
    }
};

return (
    <div className="pb-5">
    <Card className="sw-card-custom card-shadow">
      <SectionHeading section={2} title={title} total={totalCount} handlePopup={handlePopup} />
        <CardBody className="p-5">
        <div className="top_posts_row row">
            {loader ? (
            <TopPerfomingSkeleton />
            ) : postData && Object.keys(postData).length ? (
                <Splide hasTrack={false} options={settings}>
                <SplideTrack>
                { postData.map((e,index) => {
                return (
               <SplideSlide key={index+type}>    
                <div className="top_posts top_posts_new">
                    <div className="card-shadow rounded mb-1">
                    <div className="m_card_cont" onClick={() => { setpostInfo(e); handlePostpopup(true) }}>
                        <div className="post_img1 cursor-pointer position-relative">
                        <PostMedia
                            key={index}
                            postFile={e.postFile}
                            postId={e.id}
                            className={"img-fluid rounded-1"}
                        />
                        </div>
                        <div className="text-center position-relative m_user_info">
                        <div className="top-perfom-post m_grid_author d-flex justify-content-center mb-3">
                            <AuthorInfo
                                isIcon={e.network}
                                picture={e.authorImage}
                                username={e.authorName}
                                date={e.created}
                                link={e.links}
                            />
                         </div>
                        </div>

                        <div class="mt-4">
                        {type == 1 ? (
                            <div class="d-flex align-items-center justify-content-between py-2">
                            <span class="text-dark font-weight-bold font-size-md">
                                Impressions
                            </span>
                            <p class="text-dark font-weight-boldest mb-0 d-flex">
                                {e.postPerformance ? e.postPerformance : 0 }
                                <PercentDifference difference={e.difference} flag={e.flag} />
                            </p>
                            </div>
                        ) : null}

                           <div class="d-flex align-items-center justify-content-between py-2">
                            <span class="text-dark font-weight-bold font-size-md">
                            Post {type==1 ? 'Clicks' : 'Likes'} 
                            </span>
                            <p class="text-dark font-weight-boldest mb-0 d-flex">
                             { type!=1 ?      
                            <svg className="me-1" xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24" fill="#ff273c">
                            <path d="M12.001 4.52853C14.35 2.42 17.98 2.49 20.2426 4.75736C22.5053 7.02472 22.583 10.637 20.4786 12.993L11.9999 21.485L3.52138 12.993C1.41705 10.637 1.49571 7.01901 3.75736 4.75736C6.02157 2.49315 9.64519 2.41687 12.001 4.52853Z"></path></svg> : null }
                               { type!=1 ? e.reactions : e.postClicks } 
                               { type==1 ? <PercentDifference difference={e.difference} flag={e.flag} /> : null }
                            </p>
                        </div>  
 

                        <div class="d-flex align-items-center justify-content-between pt-2">
                            <span class="text-dark font-weight-bold font-size-md me-5">
                            Sentiment
                            </span>
                            <div className="text-end">

                            { e.sentiment ?     
                             <div className="d-inline-flex post-status bg-position">
                                <img
                                    className="img-fluid me-2"
                                    src={`https://cloud.tagbox.com/react-app/media/icons/emoji/emoji${
                                    SENTIMENTS_CALC(e.sentiment).item
                                    }.svg`}
                                    width={"16px"}
                                    height={"16px"}
                                />
                                <span className="overflow-hidden text-nowrap text-ellipsis">{SENTIMENTS_CALC(e.sentiment).label}</span>
                            </div> : '-' }
                          </div>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
               </SplideSlide> 
                )
            })}
            </SplideTrack>
            </Splide>
            ) : (
            <NoData />
            )}
        </div>
        </CardBody>
    </Card>

    <SeeMoreModal
        size={"xl"}
        showHide={show}
        handlePopup={handlePopup}
        title={`Top ${props.type==1 ? `Perfoming` : `Reacted` } Posts`}>
        <Suspense fallback={<></>}>
        <PostPopup moreData={props.type==1 ? getPrfomingPostMore : getreactedpostsMore} {...props} />
        </Suspense>
    </SeeMoreModal>


     <SeeMoreModal
        size={"lg"}
        showHide={showPost}
        handlePopup={handlePostpopup}
        title={ `General Information`}
        >
        <Suspense fallback={<></>}>
            <PostdetailPopup  {...postInfo} />
        </Suspense>
        </SeeMoreModal>
    </div>
);
};

export default React.memo(PerfomingPost)